.chat-page .chat-list-box{display:flex;flex-direction:column;background:var(--im-background)}.chat-page .chat-list-box .chat-list-header{height:50px;display:flex;align-items:center;padding:0 8px}.chat-page .chat-list-box .chat-list-loading{height:50px;background-color:#eee}.chat-page .chat-list-box .chat-list-loading .el-icon-loading{font-size:24px;color:var(--im-text-color-light)}.chat-page .chat-list-box .chat-list-loading .el-loading-text{color:var(--im-text-color-light)}.chat-page .chat-list-box .chat-list-loading .chat-loading-box{height:100%}.chat-page .chat-list-box .chat-list-items{flex:1}.chat-page .chat-list-box .chat-list-items ::v-deep .terminal-chat{background:linear-gradient(135deg, #667eea 0%, #764ba2 100%);border-left:3px solid #667eea}.chat-page .chat-list-box .chat-list-items ::v-deep .terminal-chat .chat-item-wrapper{background:hsla(0,0%,100%,.95)}.chat-page .chat-list-box .chat-list-items ::v-deep .terminal-chat:hover{background:linear-gradient(135deg, #764ba2 0%, #667eea 100%)}.chat-page .chat-list-box .chat-list-items ::v-deep .terminal-chat::before{content:"üñ•Ô∏è";position:absolute;right:10px;top:50%;transform:translateY(-50%);font-size:16px;opacity:.7}.chat-page .chat-list-box .chat-list-items ::v-deep .normal-chat{background:#fff}.chat-page .chat-list-box .chat-list-items ::v-deep .normal-chat:hover{background:#f5f7fa}